html,
body {
  margin: 0;
  border: 0;
  color: #333;
  background-color: #293749;
  font-family: Helvetica, "Noto Sans", "Segoe UI", "Noto Sans CJK TC", "sourcesanspro", "微軟正黑體", Arial, sans-serif;
  font-weight: 300;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500;
  margin-block-start: 0.5rem;
  margin-block-end: 0.5rem;
}

.container {
  position: relative;
  height: 100vh;
  width: 100vw;
  display: flex;
}

#particles-js {
  position: fixed;
  width: 100%;
  height: 100%;
}

.card-container {
  width: 100%;
  max-width: 720px;
  margin-right: auto;
  margin-left: auto;
  z-index: 10;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.card {
  background: #fffb;
  backdrop-filter: blur(4px);
  border-radius: 1rem;
  padding: 1rem;
  margin: 40px 15px;
}

.hide {
  opacity: 0;
  transition: all 1s ease-out;
}

.cardTop {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 15px;
}

.avatar {
  height: 100px;
  width: 100px;
  border-radius: 50%;
  background-image: url("assets/avatar.png");
  background-size: cover;
}

.basicInfo {
  text-align: left;
  margin: 0 2rem;
}

.skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.skill {
  flex: 1;
  margin: 0.5rem;
  background-color: #f7f7f7;
  border-radius: 8px;
  padding: 1rem;
}

.skill ul {
  padding-inline-start: 1rem;
}

.skill-title {
  font-weight: 700;
  color: #273b55;
}

.skill li {
  list-style: circle;
  line-height: 2rem;
  margin: 0 0.5rem;
}

.buttons {
  display: flex;
  justify-content: space-evenly;
  margin: 14px 0;
}

.buttons > .btn {
  text-decoration: none;
  background-color: #f7f7f7;
  font-size: 1.1rem;
  font-weight: 400;
  color: #2cd5c4;
  border: 0px;
  border-radius: 6px;
  padding: 10px 14px;
  transition: all 0.3s;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}

.buttons > .btn:hover {
  background-color: #fff;
  box-shadow: 4px 6px 6px rgba(0, 0, 0, 0.1);
}
